.banner {
    background-color: #11224B;
    color: white;
    font-family: "Roboto", sans-serif;
    font-weight: 400;
    font-size: 20px;
    transition-duration: 0.2s;
    display:flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    text-align: center;
    height: 7rem;
    padding-top: 1rem;
    margin-top: 1rem;
    border-style: none;
}