.container__botoes {
    position: fixed;
    bottom: 6rem; /* Ajuste conforme a altura do seu footer */
    width: 100%;
    display: flex;
    justify-content: center;
    gap: 40px;

}

.botoes {
    width: 10rem;
    height: 2rem;
    color: #11224B;
    background-color: #f8f9fa;
    border-style: solid;
    border-width: 1px;
    border-radius: 4px;
    border-color: #11224B;
    font-family: "Roboto", sans-serif;
    font-weight: 400;
    font-size: 14px;
    transition-duration: 0.2s;
}

.botoes:active {
    background-color: #11224B;
    color: #f8f9fa;
}